/* 기본 스타일 */
body {
    margin: 0;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif; /* Modern sans-serif font */
    transition: background-color 0.3s ease, color 0.3s ease; /* Smooth transition */
}

/* 다크 모드 */
[data-theme="dark"] .log-container {
    background-color: #121212; /* Darker background for better contrast */
    color: #e0e0e0; /* Light text color for readability */
}

[data-theme="dark"] h1 {
    color: #1e88e5; /* Heading color */
    font-size: 2rem;
    margin: 0;
    padding: 1rem 0;
    text-align: center;
}

[data-theme="dark"] .filter-bar {
    background-color: #1f1f1f; /* Background for filter bar */
    padding: 1rem;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    margin: 1rem;
}

[data-theme="dark"] .filter-bar input,
[data-theme="dark"] .filter-bar button {
    background-color: #333333; /* Input/button background */
    color: #e0e0e0;
    border: 1px solid #444444;
    border-radius: 5px;
    padding: 0.5rem;
    margin-right: 0.5rem;
    transition: background-color 0.3s ease;
}

[data-theme="dark"] .filter-bar input::placeholder {
    color: #9e9e9e;
}

[data-theme="dark"] .filter-bar button {
    background-color: #1e88e5; /* Button color */
    border: none;
    cursor: pointer;
}

[data-theme="dark"] .filter-bar button:hover {
    background-color: #1565c0; /* Button hover color */
}

[data-theme="dark"] .log-list {
    background-color: #1f1f1f; /* Lightened background for logs */
    padding: 1rem;
    border-radius: 8px;
    margin: 1rem;
    max-height: 70vh; /* Limit the height for scrolling */
    overflow-y: auto;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
}

[data-theme="dark"] .log-item {
    background-color: #2c2c2c; /* Background color for each log item */
    padding: 1rem;
    margin-bottom: 0.5rem;
    border-radius: 8px;
    border-left: 5px solid; /* Different border colors for log levels */
}

[data-theme="dark"] .log-item.log-level-error {
    border-color: #e57373; /* Error log border */
}

[data-theme="dark"] .log-item.log-level-info {
    border-color: #64b5f6; /* Info log border */
}

[data-theme="dark"] .log-item.log-level-warning {
    border-color: #ffb74d; /* Warning log border */
}

[data-theme="dark"] .log-timestamp {
    color: #b0bec5;
    font-weight: bold;
}

[data-theme="dark"] .log-node {
    color: #4db6ac;
}

[data-theme="dark"] .loading {
    color: #b0bec5;
    text-align: center;
    margin: 1rem 0;
}

/* 화이트 모드 */
[data-theme="light"] .log-container {
    background-color: #ffffff; /* Light background */
    color: #333333; /* Dark text color */
}

[data-theme="light"] h1 {
    color: #1e88e5; /* Heading color */
    font-size: 2rem;
    margin: 0;
    padding: 1rem 0;
    text-align: center;
}

[data-theme="light"] .filter-bar {
    background-color: #f5f5f5; /* Background for filter bar */
    padding: 1rem;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    margin: 1rem;
}

[data-theme="light"] .filter-bar input,
[data-theme="light"] .filter-bar button {
    background-color: #ffffff; /* Input/button background */
    color: #333333;
    border: 1px solid #cccccc;
    border-radius: 5px;
    padding: 0.5rem;
    margin-right: 0.5rem;
    transition: background-color 0.3s ease;
}

[data-theme="light"] .filter-bar input::placeholder {
    color: #666666;
}

[data-theme="light"] .filter-bar button {
    background-color: #1e88e5; /* Button color */
    border: none;
    cursor: pointer;
}

[data-theme="light"] .filter-bar button:hover {
    background-color: #1565c0; /* Button hover color */
}

[data-theme="light"] .log-list {
    background-color: #ffffff; /* Lightened background for logs */
    padding: 1rem;
    border-radius: 8px;
    margin: 1rem;
    max-height: 70vh; /* Limit the height for scrolling */
    overflow-y: auto;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

[data-theme="light"] .log-item {
    background-color: #ffffff; /* Background color for each log item */
    padding: 1rem;
    margin-bottom: 0.5rem;
    border-radius: 8px;
    border-left: 5px solid; /* Different border colors for log levels */
}

[data-theme="light"] .log-item.log-level-error {
    border-color: #f8d7da; /* Error log border */
}

[data-theme="light"] .log-item.log-level-info {
    border-color: #d0e9ff; /* Info log border */
}

[data-theme="light"] .log-item.log-level-warning {
    border-color: #fff3cd; /* Warning log border */
}

[data-theme="light"] .log-timestamp {
    color: #6c757d;
    font-weight: bold;
}

[data-theme="light"] .log-node {
    color: #007bff;
}

[data-theme="light"] .loading {
    color: #6c757d;
    text-align: center;
    margin: 1rem 0;
}

/* 테마 토글 버튼 스타일 */
.theme-toggle {
    position: fixed;
    top: 20px;
    right: 20px;
    padding: 10px 20px;
    background-color: #1e88e5;
    color: #ffffff;
    border: none;
    border-radius: 50px; /* Rounded corners for a pill-like appearance */
    cursor: pointer;
    font-size: 1rem;
    font-weight: bold;
    transition: background-color 0.3s ease, transform 0.3s ease;
}

.theme-toggle:hover {
    background-color: #1565c0;
}

.theme-toggle:active {
    transform: scale(0.95);
}
